.container {
  padding: 90px 16px 100px;
  box-sizing: border-box;
  height: 100%;

  .post_list {
    display: grid;
    gap: 40px;
  }

  .post_item {
    img {
      width: 100%;
      height: calc((100vw * 276) / 390);
      display: block;
      object-fit: cover;
      margin-bottom: 24px;
    }
  }

  .title_box {
    position: relative;
    h3 {
      font-size: 5.333vw;
      font-weight: 400;
      line-height: normal;
      margin-bottom: 2.133vw;
      color: #fff;
    }

    p {
      font-size: 4.267vw;
      font-weight: 400;
      line-height: normal;
      color: #a4a4a4;
      max-width: 80%;
    }
  }

  .edit_btns {
    position: absolute;
    top: 50%;
    right: 0;
    display: flex;
    color: #fff;
    gap: 6px;

    button {
      font-size: 14px;
      color: #fff;
      font-weight: 400;
      line-height: normal;
      position: relative;

      &:hover::after {
        content: '';
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: #fff;
      }
    }
  }

  .write_btn {
    color: #fff;
    width: 150px;
    height: 62px;
    margin: 0 0 30px auto;
    display: block;
    border: 1px solid #fff;
    line-height: 62px;
    text-align: center;
    font-size: 14px;
    font-weight: 400;
    box-sizing: border-box;
  }

  @media screen and (min-width: 1280px) {
    padding: 170px 55px 200px;

    .post_list {
      grid-template-columns: repeat(3, 1fr);
      gap: 100px 24px;
    }

    .post_item {
      img {
        height: calc((100vw * 732) / 1920);
        max-height: 732px;
        margin-bottom: 30px;
      }
    }

    .title_box {
      h3 {
        font-size: 30px;
        margin-bottom: 14px;
      }

      p {
        font-size: 20px;
      }
    }

    .write_btn {
      width: 170px;
      font-size: 18px;
      margin-bottom: 80px;
    }

    .edit_btns {
      top: 53px;
    }
  }
}
